/**
 * 
 * @param fn  Function
 * @param delay 
 * @param context 
 */
const throttle = (fn: Function, delay: number, context: Object): any => {
  let lastTime = 0
  return function(...args: any) {
    const now = new Date().getTime()
    if(now - lastTime > delay) {
      fn.call(context, ...args)
      lastTime = now
    }
  }
}

export default throttle